.navbar {
	background-color: #02122a;
	width: 32px;
	height: 100vh;
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
}

.menu-bars {
	font-size: 2rem;
	background: none;
}

.nav-menu {
	background-color: #02122a;
	width: 250px;
	height: 100vh;
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 850ms;
}

.nav-menu.active {
	left: 0;
	transition: 350ms;
	z-index: 10;
}

.nav-text {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}

.nav-text a {
	text-decoration: none;
	color: #f5f5f5;
	font-size: 18px;
	width: 91%;
	height: 100%;
	display: flex;
	align-items: center;
	padding: 0 16px;
	border-radius: 4px;
}

.bottom {
	position: absolute;
	bottom: 0;
	width: 86%;
}

.nav-text a:hover {
	background-color: #1a83ff;
}

.nav-menu-items {
	width: 100%;
	padding: 0.5rem;
}

.navbar-toggle {
	background-color: #02122a;
	width: 100%;
	height: 4rem;
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

span {
	margin-left: 16px;
}

