.day{
    padding: 1rem;
    margin: 1% auto;
    height: 28vh;
    width: 28vh;
    border-radius: 15%;
    background-color: rgba(153, 0, 255, 0.8);
    box-sizing: border-box;
    border: 9px solid white;
} 

.clue {
    display: none;
    position: absolute;
}

.defaultDay:hover .clue {
    display: block;
}
.defaultDay:hover {
    background-color: rgba(153, 0, 255, 1);
    transition: background-color, 200ms;
}

.day:hover {
    cursor: pointer;
}

